.container {
  width: 300px;
  margin: 10px;
  border: 2px solid;
  border-radius: 8px;
  border-color: rgb(82, 84, 85);
  padding: 20px;
  align-content: center;
  position: relative;
  background-color: rgb(214, 214, 214);
}

.boton {
  background-color: crimson;
  border-radius: 5px;
  border-color: crimson;
  color: white;
  position: absolute;
  top: 8px;
  right: 16px;
}

.cityName {
  font-size: 25px;
  font-weight: bold;
  align-self: center;
}

p {
  font-size: 15px;
  font-weight: bold;
  align-self: flex-end;
}

.min {
  position: absolute;
  left: 20px;
}
.max {
  position: absolute;
  right: 20px;
}
